@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Lato", sans-serif;
  background-color: #F8F6F5;
}

@media screen and (max-width: 768px) {
  body {
    font-size: 90%;
  }
}

* {
  transition: .125s ease-in;
}

h1, h2 {
  font-family: "Oswald", sans-serif;
}

.bg-yellow {
  background-color: #FFD647;
}

.trevette-logo {
  height: 35px;
  width: 138px;
  background-image: url("/gfx/logo.svg");
  background-size: cover;
}

@media screen and (min-width: 769px) {
  .trevette-logo {
    height: 50px;
    width: 197px;
  }
}

.navbar {
  max-width: 1200px;
  margin: 0 auto;
}

.navbar-container {
  background: #FFD647;
}

@media screen and (max-width: 992px) {
  #navbarMenu {
    padding: 1rem 1.25rem;
  }
}

.main-container {
  margin-top: 4rem;
  margin-bottom: 4rem;
  display: flex;
  flex-direction: row;
}

@media screen and (max-width: 992px) {
  .main-container {
    flex-direction: column;
  }
}

.wrap-main {
  flex: 6 1;
  order: 0;
}

.wrap-garage {
  flex: 3 1;
  margin-left: 1rem;
  border-radius: 5px;
}

@media screen and (max-width: 992px) {
  .wrap-garage {
    margin-left: 0;
  }
  .wrap-garage h2 {
    margin-top: 1rem;
  }
}

.wrap-garage .module {
  height: 120px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0px 2px 5px rgba(85, 84, 81, 0.25);
  border-radius: 5px;
  margin: 1rem 0;
}

.object-wrap {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(85, 84, 81, 0.25);
  overflow: hidden;
}

.object {
  border-radius: 5px;
}

.object__photo {
  height: 300px;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.object__info {
  padding: 2rem;
}

.object__info--salesinfo {
  margin-bottom: 2rem;
}

.object__info--salesinfo .flavor-title {
  margin-top: .5rem;
}

.object__info--salesinfo button {
  padding: .75rem 0;
  font-family: "Oswald", sans-serif;
  box-shadow: 0 0.375em 0 #FFD647;
  border: 0.125rem solid #FFD647;
  background-color: rgba(255, 214, 71, 0.2);
}

.object__info--salesinfo button:hover {
  background-color: rgba(255, 214, 71, 0.35);
  box-shadow: 0 0.375em 0 #faa230;
  border: 0.125rem solid #faa230;
}

.object__info--salesinfo .badge {
  font-size: 90%;
  margin-right: .5rem;
  padding: .35rem .6rem;
}

.object__info--salesinfo .price {
  color: #555451;
  text-align: center;
  margin-top: .5rem;
}

.object__info--technical .title {
  margin-top: 1rem;
}

.object__info--technical .box {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 1200px) {
  .object__info--technical .box {
    flex-direction: row;
  }
}

@media screen and (min-width: 1200px) {
  .object__info--technical .box:not(:last-of-type) {
    margin-bottom: .5rem;
  }
}

.object__info--technical .box .module {
  margin-bottom: .5rem;
  flex: 1 1;
  display: flex;
  justify-content: space-between;
  padding-bottom: .15rem;
  border-bottom: 1px solid #a3a3a1;
}

@media screen and (min-width: 1200px) {
  .object__info--technical .box .module {
    margin-right: .5rem;
    margin-bottom: 0;
  }
}

.object__info--technical .box .module__noborder {
  border-bottom: 0;
}

.object__info--technical .box .module .left .fa, .object__info--technical .box .module .left .fas {
  margin-right: .5rem;
  color: #a66004;
}

.object__info--technical .box .module .right span {
  color: #232123;
}

.object__gallery {
  display: flex;
  flex: row 1 wrap;
  margin: 1rem 0rem;
}

@media screen and (min-width: 768px) {
  .object__gallery {
    padding: 1rem 4rem;
  }
}

@media screen and (min-width: 992px) {
  .object__gallery {
    padding: 1rem 0rem;
  }
}

@media screen and (min-width: 1200px) {
  .object__gallery {
    padding: 1rem 6rem;
  }
}

.object__gallery .gallery-img {
  flex: 1 1;
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
